<template>
  <div class="follower_con" style="padding: 10px">
    <h4>팔로워</h4>
    <input @input="search($event.target.value)" placeholder="🔎" />
    <div v-for="(a, i) in follower" :key="i" class="post-header">
      <div
        class="profile"
        :style="`background-image : url(${follower[i].image})`"
      ></div>
      <span class="profile-name">{{ follower[i].name }}</span>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import axios from "axios";
import { onMounted } from "@vue/runtime-core";
export default {
  name: "Follower",
  setup() {
    let follower = ref([]);
    let followerOriginal = ref([]);

    onMounted(() => {
      axios.get("/follower.json").then((a) => {
        follower.value = a.data;
        followerOriginal.value = [...a.data];
      });
    });

    function search(val) {
      let newFollower = followerOriginal.value.filter((a) => {
        return a.name.indexOf(val) != -1;
      });
      follower.value = [...newFollower];
    }
    return { follower, search };
  },
  data() {
    return {};
  },
  props: {
    // followerInfo : Array,
  },
};
</script>

<style>
  .follower_con{
    position: sticky;
    top: 0px;
    height: 300px;
  }

  .profile-name{
    margin-left: 10px;
  }
</style>
